<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true"></a> </h3>
<p>,  checkbox .</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox class=\&quot;yuki-checkbox\&quot; v-model=\&quot;checked\&quot;&gt;&lt;/el-checkbox&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            checked: true\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox class="yuki-checkbox" v-model="checked"></el-checkbox>

</body></html></div>
                            <p> <code>el-checkbox</code>  <code>v-model</code> ,  <code>checkbox</code> ,  <code>Boolean</code>,  <code>true</code>.</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="duo-xuan-kuang-zu"><a class="header-anchor" href="#duo-xuan-kuang-zu" aria-hidden="true"></a> </h3>
<p>, .</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox-group v-model=\&quot;checkList\&quot;&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot; A\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot; B\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot; C\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot;\&quot; disabled&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; label=\&quot;\&quot; disabled&gt;&lt;/el-checkbox&gt;\n&lt;/el-checkbox-group&gt;\n\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nexport default = {\n    data() {\n        return {\n            checkList: ['', ' A']\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox-group v-model="checkList">
    <el-checkbox class="yuki-checkbox" label=" A"></el-checkbox>
    <el-checkbox class="yuki-checkbox" label=" B"></el-checkbox>
    <el-checkbox class="yuki-checkbox" label=" C"></el-checkbox>
    <el-checkbox class="yuki-checkbox" label="" disabled></el-checkbox>
    <el-checkbox class="yuki-checkbox" label="" disabled></el-checkbox>
</el-checkbox-group>


</body></html></div>
                            <p><code>checkbox-group</code>  checkbox ,  Group  <code>v-model</code>  <code>Array</code> . <code>el-checkbox</code>  <code>label</code>  checkbox , ,  checkbox . <code>label</code> , , .</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkList"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">" A"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">" B"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">" C"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">""</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">""</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox-group</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> = {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkList</span>: [<span class="hljs-string">''</span>, <span class="hljs-string">' A'</span>]
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="indeterminate-zhuang-tai"><a class="header-anchor" href="#indeterminate-zhuang-tai" aria-hidden="true"></a> Indeterminate </h3>
<p><code v-pre>indeterminate</code>  checkbox , .</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox class=\&quot;yuki-checkbox\&quot; :indeterminate=\&quot;isIndeterminate\&quot; v-model=\&quot;checkAll\&quot; @change=\&quot;handleCheckAllChange\&quot;&gt;&lt;/el-checkbox&gt;\n&lt;div style=\&quot;margin:15px 0\&quot;&gt;&lt;/div&gt;\n&lt;el-checkbox-group v-model=\&quot;checkedCities\&quot; @change=\&quot;handleCheckedCitiesChange\&quot;&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; v-for=\&quot;city in cities\&quot; :label=\&quot;city\&quot; :key=\&quot;city\&quot;&gt;&lt;/el-checkbox&gt;\n&lt;/el-checkbox-group&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nconst cityOptions = ['','','','']\nexport default = {\n    data() {\n        return {\n            checkAll: false,\n            checkedCities: ['', ''],\n            cities: cityOptions,\n            isIndeterminate: true,\n        }\n    },\n    methods:{\n        handleCheckAllChange(val) {\n            this.checkedCities = val ? cityOptions : [];\n            this.isIndeterminate = false;\n        },\n        handleCheckedCitiesChange(value) {\n            let checkedCount = value.length;\n            this.checkAll = checkedCount === this.cities.length\n            this.isIndeterminate = checkedCount &gt; 0 &amp;&amp; checkedCount &lt; this.cities.length\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox class="yuki-checkbox" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
<div style="margin:15px 0"></div>
<el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
    <el-checkbox class="yuki-checkbox" v-for="city in cities" :label="city" :key="city"></el-checkbox>
</el-checkbox-group>

</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">:indeterminate</span>=<span class="hljs-string">"isIndeterminate"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkAll"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckAllChange"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin:15px 0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckedCitiesChange"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">const</span> cityOptions = [<span class="hljs-string">''</span>,<span class="hljs-string">''</span>,<span class="hljs-string">''</span>,<span class="hljs-string">''</span>]
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> = {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkAll</span>: <span class="hljs-literal">false</span>,
            <span class="hljs-attr">checkedCities</span>: [<span class="hljs-string">''</span>, <span class="hljs-string">''</span>],
            <span class="hljs-attr">cities</span>: cityOptions,
            <span class="hljs-attr">isIndeterminate</span>: <span class="hljs-literal">true</span>,
        }
    },
    <span class="hljs-attr">methods</span>:{
        handleCheckAllChange(val) {
            <span class="hljs-keyword">this</span>.checkedCities = val ? cityOptions : [];
            <span class="hljs-keyword">this</span>.isIndeterminate = <span class="hljs-literal">false</span>;
        },
        handleCheckedCitiesChange(value) {
            <span class="hljs-keyword">let</span> checkedCount = value.length;
            <span class="hljs-keyword">this</span>.checkAll = checkedCount === <span class="hljs-keyword">this</span>.cities.length
            <span class="hljs-keyword">this</span>.isIndeterminate = checkedCount &gt; <span class="hljs-number">0</span> &amp;&amp; checkedCount &lt; <span class="hljs-keyword">this</span>.cities.length
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="ke-xuan-xiang-mu-shu-liang-de-xian-zhi"><a class="header-anchor" href="#ke-xuan-xiang-mu-shu-liang-de-xian-zhi" aria-hidden="true"></a> </h3>
<p> <code v-pre>min</code>  <code v-pre>max</code> .</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;el-checkbox-group v-model=\&quot;checkedCities\&quot; @change=\&quot;handleCheckedCitiesChange\&quot; :min=\&quot;1\&quot; :max=\&quot;2\&quot;&gt;\n    &lt;el-checkbox class=\&quot;yuki-checkbox\&quot; v-for=\&quot;city in cities\&quot; :label=\&quot;city\&quot; :key=\&quot;city\&quot;&gt;&lt;/el-checkbox&gt;\n&lt;/el-checkbox-group&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\nconst cityOptions = ['','','','']\nexport default = {\n    data() {\n        return {\n            checkedCities: ['', ''],\n            cities: cityOptions,\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="show-demo" slot="source"><html><head></head><body><el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange" :min="1" :max="2">
    <el-checkbox class="yuki-checkbox" v-for="city in cities" :label="city" :key="city"></el-checkbox>
</el-checkbox-group>

</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckedCitiesChange"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"2"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"yuki-checkbox"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">const</span> cityOptions = [<span class="hljs-string">''</span>,<span class="hljs-string">''</span>,<span class="hljs-string">''</span>,<span class="hljs-string">''</span>]
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> = {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkedCities</span>: [<span class="hljs-string">''</span>, <span class="hljs-string">''</span>],
            <span class="hljs-attr">cities</span>: cityOptions,
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>
<style>
.demo-check-box .source{
    position: relative;
}
.demo-check-box .yuki-checkbox{
    margin-bottom: 10px;
}
.demo-box.demo-alert .el-alert{
    margin: 20px 0 0;
}
.demo-box.demo-alert .el-alert:first-child{
    margin: 0;
}
</style>
<script>
const cityOptions = ['','','','']
module.exports = {
    data() {
        return {
            checked: true,
            checkList: ['', ' A'],
            checkAll: false,
            checkedCities: ['', ''],
            cities: cityOptions,
            isIndeterminate: true,
        }
    },
    methods:{
        handleCheckAllChange(val) {
            this.checkedCities = val ? cityOptions : [];
            this.isIndeterminate = false;
        },
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length
        }
    }
}
</script>


